
input,
select {
  margin-bottom: 10px;
}
a {
  color: #337ab7;
}
.text {
  color: black;
  font-size: 20px;
}
.auth-intro-header {
  display: inline-block;
  opacity: 0.9;

  margin: 0px !important;
  width: 100%;
  height: 100vh;
  padding-top: 50px;
  padding-bottom: 50px;

  background: url(/static/logos/tues_building.jpg) no-repeat center center fixed;
  background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  -webkit-background-size: cover;

  text-align: center;
}
.auth-intro-header.overlay:after {
  top: 0;
  left: 0;
  position: fixed;
  overflow: hidden;
}
.overlay {
  position: relative;
}
.overlay:after {
  content: "";
  z-index: -2;
  top: 0;
  left: 0;
  opacity: 0.7;
  width: 100%;
  height: 100vh;
  background-color: indigo;
}
#reset-password-form {
  opacity: 0.9;
  border-radius: 5px;
  margin-top: 7em;
  padding: 5%;
  background-color: white;
  text-align: center;
}
#pwd-container .form-wrapper,
#go-home-button a {
  color: white;
}
#pwd-container {
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
}



